#optionContent {
  float: left;
  height: 100%;
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

#optionContent .optionSection {
  padding: 10px;
  height: auto;
  width: calc(100% - 40px);
  background-color: var(--card-background-color);
  color: var(--card-color);
  box-shadow: var(--boxshadow);
  word-wrap: break-word;
  margin-bottom: 10px;
}

#options .optionSection {
  padding: 10px;
  height: auto;
  width: auto;
  background-color: var(--card-background-color);
  color: var(--card-color);
  box-shadow: var(--boxshadow);
  word-wrap: break-word;
  margin-bottom: 10px;
  margin-right: 10px;
}

#options .optionSection span {
  font-size: 12px;
}

#options .inputfile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

#options .inputfile:focus + label,
#options .inputfile + label:hover {
  box-shadow: var(--boxshadow-hover);
}

#optionContent button,
#optionContent a,
#options .inputfile + label {
  display: inline-block;
  background-color: var(--button-background-color);
  height: 28px;
  width: auto;
  border: none;
  border-radius: 5px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 5px 5px 5px 0px;
  text-align: center;
  font-family: inherit;
  font-size: 14px;
  color: var(--button-color);
  cursor: pointer;
  line-height: 26px;
}
#optionContent .optionSection a.patreon {
  background-color: rgb(232, 91, 70);
}
#optionContent .optionSection a.discord {
  background-color: #7289da;
}

.optionSection button {
  background-color: var(--button-background-color);
  border-bottom: 2px solid transparent;
}

button:hover {
  box-shadow: var(--boxshadow-hover);
  cursor: pointer;
}
#options {
  height: auto;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-items: flex-start;
  align-content: flex-start;
}

#options::-webkit-scrollbar {
  width: 6px;
  background-color: var(--scrollbar);
  border-radius: 5px;
}

#options::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-thumb);
  border-radius: 5px;
}

.loadingScreen {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: var(--app-background-color);
  z-index: 100;
}

.loadingScreen .loadingTab {
  position: absolute;
  top: calc(50% - 50px);
  left: calc(50% - 200px);
  width: 400px;
  height: 100px;
  text-align: center;
  color: var(--card-color);
}
