#leftNav {
  bottom: 15px;
  left: 15px;
  width: 50px;
  height: 50px;
  min-height: 50px;
  position: fixed;
  background-color: var(--card-background-color);
  box-shadow: var(--boxshadow-hover);
  border-radius: 50px;
  z-index: 10;
  overflow: hidden;
  transition: 0.3s;
}

#leftNav.open {
  height: 375px;
}

#leftNav a {
  height: 40px;
  width: 40px;
  margin: 5px;
  text-align: center;
  line-height: 40px;
  font-size: 14px;
  color: white;
  text-shadow: 0px 0px 5px #000000;
  display: block;
  cursor: pointer;
}

#leftNav a img {
  width: 20px;
  margin-top: 10px;
  margin-left: 10px;
  float: left;
}

#leftNav a svg {
  font-size: 16px;
}

.smallIcon {
  color: #8000ff;
  font-size: 40px;
}

#leftNav a:hover,
#leftNav a.menuItemActiv {
  background-color: var(--navbar-icon);
  border-radius: 50px;
  transition: 0.5s;
}

#leftNav .gradIcon {
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 70px;
  border: none;
  padding: 0px;
}

#leftNav .gradIcon svg * {
  fill: url(#rgg);
}

#leftNav .seperator {
  height: 3px;
  width: 100%;
  z-index: 10;
  position: absolute;
  left: 0px;
  border-bottom: 1px dashed #fff;
}
